body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

header {
    background-color: #333;
    color: #fff;
    padding: 10px;
    width: 100%;
    text-align: center;
}

header button {
    background-color: #007BFF;
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
}

header button:hover {
    background-color: #0056b3;
}

main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
}

#product-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 20px;
    width: 100%;
    max-width: 1200px;
}

.product-item {
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
    padding: 10px;
    background-color: #fff;
}

.product-item img {
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.product-item h2 {
    margin: 10px 0;
}

.product-item p {
    margin: 5px 0;
}

.stars {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.stars .fa-star {
    font-size: 20px;
    color: #ccc;
}

.stars .fa-star.active {
    color: orange;
}

footer {
    background-color: #333;
    color: #fff;
    padding: 20px;
    width: 100%;
    text-align: center;
    margin-top: auto;
}
